// 
_this.$sortBy = function(attr, rev) {
	// 
	if (rev == undefined) {
		rev = 1;
	} else {
		rev = rev ? 1 : -1;
	}
	return function(a, b) {
		let n1 = a[attr];
		let n2 = b[attr];
		if (n1 < n2) {
			return rev * -1;
		}
		if (n1 > n2) {
			return rev * 1;
		}
		return 0;
	};
};
